import { utcToZonedTime } from 'date-fns-tz';

class Clock {
  constructor(clockEl) {
    this.clockEl = clockEl;
    this.UI = {};
    this.initLoadFinished = false;
    this.initializeClock();
  }
  
  updateClock = () => {
    const date = new Date();
    const now = utcToZonedTime(date, this.clockEl.dataset.locale);
    const hourDeg = (now.getHours() + now.getMinutes() / 60) / 12 * 360;
    const minDeg = (now.getMinutes() + now.getSeconds() / 60) / 60 * 360;
    const secDeg = (now.getSeconds() + now.getMilliseconds()/1000) / 60 * 360;
    // const secDeg = now.getSeconds() / 60 * 360;
    // Update TRANSITIONS
    // if (this.initLoadFinished === true) {
    //   secDeg === 0 ? this.UI.second.style.transition = 'none' : this.UI.second.style.  transition = 'transform 250ms cubic-bezier(0.4, 2.08, 0.55, 0.44)';
    // }
    this.UI.date.textContent = utcToZonedTime(date, this.clockEl.dataset.locale).getDate();
    this.UI.am_pm.textContent = utcToZonedTime(date, this.clockEl.dataset.locale).getHours() > 12 ? 'PM' : 'AM';
    this.UI.hour.style.transform = `rotate(${hourDeg}deg)`;
    this.UI.minute.style.transform = `rotate(${minDeg}deg)`;
    this.UI.second.style.transform = `rotate(${secDeg}deg)`;
    requestAnimationFrame(this.updateClock);
    this.initLoadFinished = true;
  }

  initializeClock() {
    this.clockEl.innerHTML = `<svg class="face" width="300" height="300" viewBox="-150 -150 300 300">
      <circle class="ring ring--minutes" r="145" pathlength="60" />
      <text x="50" y="-5" class="date">0</text>
      <text x="50" y="10" class="am-pm">AM</text>
      <circle class="ring ring--hours" r="145" pathlength="144" />
      <circle class="ring ring--hours" r="145" pathlength="144" />
      <line class="hand hand--minute" x1="0" y1="2" x2="0" y2="-110" />
      <line class="hand hand--hour" x1="0" y1="2" x2="0" y2="-60" />
      <circle class="ring ring--center" r="3" />
      <line class="hand hand--second" x1="0" y1="12" x2="0" y2="-130" />
    </svg>`;
    this.UI.date = this.clockEl.querySelector('.date');
    this.UI.am_pm = this.clockEl.querySelector('.am-pm');
    this.UI.second = this.clockEl.querySelector('.hand--second');
    this.UI.minute = this.clockEl.querySelector('.hand--minute');
    this.UI.hour = this.clockEl.querySelector('.hand--hour');
    requestAnimationFrame(this.updateClock);
  }
}

document.querySelectorAll('.clock').forEach(el => new Clock(el));